@import url(https://fonts.googleapis.com/css?family=Oswald);
* {
    box-sizing: border-box;
  }

  html,
body {
  min-height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-weight: 600;
    line-height: 1.5;
    font-size: 18px;
    letter-spacing: .1em;
    background: var(--light);
    font-family: Arial, Helvetica, sans-serif;
  }

main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}

/*typography*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
    font-size: 2.25rem;
  }
  
  h2 {
    font-size: 2rem;
    font-family: 'Abril Fatface';
    color: var(--darker);
  }
  
  h3 {
    font-size: 1.75rem;
  }
  
  h4 {
    font-size: 1.5rem;
  }
  
  h5 {
    font-size: 1.25rem;
  }
  
  h6 {
    font-size: 1rem;
  }
  
  p {
    margin-top: 0;
  }
  
  a {
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  header {
    background: var(--dark);
  }
  
  header a {
    color: var(--light);
  }

  button {
    font-size: 1rem;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border-radius: .5rem;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: var(--secondary);
  }
  
  button:hover {
    opacity: .8;
  }
  
  button[disabled] {
    cursor: default;
    opacity: .8;
  }
  
  img {
    max-width: 100%;
  }
  
  #avatar {
    max-height:300px;
    border-radius: 300px;
    }
  
  span[role="img"] {
    cursor: default;
  }
  
  form {
    width: 40%;
  }
  
  input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid var(--dark);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
  }
  
  input:focus {
    border: 1px solid var(--secondary);
    box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
  }
  
  li {
    list-style: none;
  }
  
  .nav-font {
    font-family: 'Oswald' ;
    font-size: 1.5em;
  }
  
  .nav-title-font {
    font-family: 'Oswald';
    font-size: 1.9em;
  }
  
  .top-title {
    font-family: 'Oswald';
    margin-top: 20px;
  }
  
  .navitem-indent {
    padding-right: 50px;
  }
  
  .navbar-nav > li{
    margin-left:30px;
    margin-right:30px;
  }
  
  nav a:hover {
    text-decoration: none;
  }
  
  nav  li {
    cursor: pointer;
  }
  
  textarea {
    width: 600px;
    height: 120px;
    border: 3px solid #cccccc;
    padding: 5px;
  }
  
  .container {
    width: 85%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .flex-row {
    display: flex;
    flex-wrap: wrap;
  }
  
  .space-between {
    justify-content: space-between;
  }
  
  .flex-end {
    justify-content: flex-end;
  }
  
  .mx-1 {
    margin-right: var(--spacing-one);
    margin-left: var(--spacing-one);
  }
  
  .mx-2 {
    margin-right: var(--spacing-two);
    margin-left: var(--spacing-two);
  }
  
  .my-1 {
    margin-top: var(--spacing-one);
    margin-bottom: var(--spacing-one);
  }
  
  .my-2 {
    margin-top: var(--spacing-two);
    margin-bottom: var(--spacing-two);
  }
  
  .my-5 {
    margin-top: var(--spacing-five);
    margin-bottom: var(--spacing-five);
  }
  
  .px-1 {
    padding-right: var(--spacing-one);
    padding-left: var(--spacing-one);
  }
  
  .px-2 {
    padding-right: var(--spacing-two);
    padding-left: var(--spacing-two);
  }
  
  .py-1 {
    padding-top: var(--spacing-one);
    padding-bottom: var(--spacing-one);
  }
  
  .py-2 {
    padding-top: var(--spacing-two);
    padding-bottom: var(--spacing-two);
  }
  
  .wrapper {
    padding-top: 50px;
    justify-content: space-around;
    height: 100%;
    display: flex;
    overflow: auto;
    flex-flow: row wrap;
    padding: 10px;
    align-content: flex-start;
  }
  
  .card {
    background-color: var(--white);
    border-radius: 5px;
    height: 400px;
    margin: 2rem;
    position: relative;
    width: 400px;
    box-shadow: 0 2px 2px var(--cultured), 0 6px 12px var(--cultured);
  }
  
  .card .img-container {
    height: 75%;
    overflow: hidden;
    text-align: center;
    border-radius: 10px;
    background-color: var(--white);
  }
  
  .card .img-container img {
    width: 100%;
    padding: 25px;
    margin-bottom: 20px;
    background-color: var(--white);
  }
  
  .card .content {
    font-size: 18px;
    background-color: var(--white);
    color: var(--cadet-blue);
    font-weight: bold;
    text-align: center;
  }
  
  .card-title{
    margin-top: 10px;
    color: var(--eerie-black);
  }
  
  .project-title {
    color: var(--cultured);
    background-color: var(--white);
  }
  
  .project-icons-container {
    background-color: var(--white);
  }
  
  .project-icon {
    background-color: var(--white);
    padding: 5px;
  }
  
  .topics {
    font-size: small;
    font-style: italic;
    color: var(--cultured);
    background-color: var(--white)
  }
  
  .footer { 
    display: flex; 
    align-items: center; 
    justify-content: center; 
  } 
  
  .icon {
    padding: 10px;
    border-radius: 15px;
  }
  
  
  .img-thumbnail {
    width: auto;
    height: 300px;
    margin-bottom: 50px;
    cursor: pointer;
  }
  
  .modalBackdrop {
    background: rgba(0, 0, 0, .65);
    bottom: 0;
    left: 0;
    overflow: auto;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
  }
  
  .modalContainer {
    background: var(--darker);
    border-radius: 5px;
    max-width: 100%;
    margin: 50px auto;
    padding: 15px;
    width: 960px;
  }
  
  .modalTitle {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 25px;
  }
  
  button {
    background: var(--primary);
    border: 0;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 30px;
    padding: 10px 15px;	
  }
  
  a {
    border-radius: 5px;
    color: var(--primary);
    font-weight: 600;
    padding: 2px 4px;
    margin-left: -2px;
    margin-right: -2px;
    
  }
  
  .navActive {
    color: var(--secondary);
  }

  .resume-logo {
    border-radius: 15px;
  }
